* {
    box-sizing: border-box;
}
html {
    line-height: 1.5em;
}
body {
    margin: 0;
}
ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}
a {
    text-decoration: none;
    color: inherit
}
button {
    & :focus {
        outline: none;
    }
    border: none;
}




